    <div class="content">
      <img class="picture" :src="aboutUsInfo.picture" alt="">
      <div class="companyProfile">
        <div class="companyProfileTitle">{{ aboutUsInfo.companyProfile.title }}</div>
        <div class="companyProfileDesc">{{ aboutUsInfo.companyProfile.desc }}</div>
      </div>
    </div>
    <div class="visonFooter">
      <div class="visonTitle">{{ aboutUsInfo.vision.title }}</div>
      <ul>
        <li v-for="(item,index) in aboutUsInfo.vision.list" :key="index">{{ item }}</li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: "AboutUs",
  props: {
    aboutUsInfo: {
      type: Object, defaults() {
        return {}
      }
    }
  },
  data() {
    return {
    }
  }
}
</script>
<style lang="scss" scoped>
.container {
  width: 100%;
  background-color: #2A45A7;

  .title {
    padding-top: 200px;
    margin: 0 auto;
    font-size: 64px;
    font-family: "SourceHanSansSC-Bold-2";
    font-weight: bold;
    color: #EAFF74;

    .titleHeader {
      color: #FFFFFF;
    }
