  .sutitle {
    height: 130px;
    margin: 160px 0;
    display: flex;
    align-items: center;

    .icon {
      flex: 1;
      display: flex;
      justify-content: flex-end;

      .subtitleIcon {
        width: 438px;
        height: 81.69px;
      }
    }
    
    .line {
      width: 1px;
      height: 130px;
      background-color: #FFFFFF;
      margin: 0 59px;
    }

    .sutitleText {
      flex: 1;
      font-size: 56px;
      font-family: "SourceHanSansSC-Bold-2";
      font-weight: bold;
      color: #FFFFFF;
      text-align: left;
