  .content {
    position: relative;

    .picture {
      width: 1400px;
      height: 668.56px;
      border-radius: 24px;
    }

    .companyProfile {
      position: absolute;
      box-sizing: border-box;
      top: 534px;
      left: 764px;
      width: 840px;
      background: #E43363;
      border-radius: 24px;
      padding: 95px 116px;

      .companyProfileTitle {
        text-align: left;
        font-size: 48px;
        font-family: "SourceHanSansSC-Bold-2";
        font-weight: bold;
        color: #FFFFFF;
      }

      .companyProfileDesc {
        color: #FFFFFF;
        text-align: left;
        margin-top: 40px;
        font-size: 24px;
        font-family: "SourceHanSansSC-Bold-2";
        font-weight: bold;
      }
    }
