    ul {
      margin-top: 51px;
    }

    li {
      width: 400px;
      word-wrap: break-word;
      margin: 10px 0;
      margin-left: 40px;
      list-style-type: disc;
      text-align: left;
      font-size: 24px;
      font-family: "SourceHanSansSC-Bold-2";
      font-weight: bold;
      color: #FFFFFF;
    }
  }
}
</style>



